@include tablet {
    .contenedor-anuncios {
        @include grid(3, 2rem);
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    border: 1px solid darken($grisOscuro, 10);
    margin-bottom: 3rem;
    text-align: center;
    .contenido-anuncio {
        padding: 2rem;
    }
    h3,
    p {
        margin: 0;
    }
}

.precio {
    color: $verde;
    font-weight: $bold;
    font-size: 2.7rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    li {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            flex: 0 0 4rem;
            margin-right: 1rem;
        }
        p {
            font-weight: $black;
        }
    }
}